<template>
  <div class="manage">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item>人员管理</a-breadcrumb-item>
        <a-breadcrumb-item>管理员管理</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <div class="serch_field">
      <a-form-model
        layout="inline"
      >
        <a-form-model-item style="width: 100%;">
          <a-form-model-item label="用户状态">
            <a-select
              v-model="params.status"
              :options="status"
              style="width: 150px;"
            ></a-select>
          </a-form-model-item>
          <a-form-model-item>
            <a-input
              placeholder="请输入管理员名称"
              v-model="params.content"
              style="width: 220px;"
            ></a-input>
          </a-form-model-item>
          <a-button type="primary" @click="search" style="margin-right: 12px;">搜索</a-button>
          <a-button @click="reset">重置</a-button>
        </a-form-model-item>

        <div style="display: flex; justify-content: space-between; padding: 12px 0;">
          <a-form-model-item>
            <a-button type="primary" style="margin-right: 20px;" @click="toCreateManager">创建管理员</a-button>
            <a-button type="primary" @click="viewRecord">操作记录</a-button>
          </a-form-model-item>
          <a-form-model-item>
            <a-button type="primary" style="margin-right: 20px;" @click="banManager(selectedRowKeys, 1)">批量启用</a-button>
            <a-button type="primary" @click="banManager(selectedRowKeys, 2)">批量禁用</a-button>
          </a-form-model-item>
        </div>

      </a-form-model>
    </div>
    
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
      :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
    >
      <template slot="gender" slot-scope="text">
        {{ text.gender === 1 ? '男' : '女'}}
      </template>
      <template slot="status" slot-scope="text">
        {{ text.status === 1 ? '正常' : '已禁用' }}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="viewManagerDetail(text.id)">查看</a-button>
        <a-button type="link" @click="banManager(text)">{{ text.status === 1 ? '禁用' : '启用' }}</a-button>
        <a-button type="link" @click="deleteManager(text.id)">删除</a-button>
      </template>
    </s-table>

    <!-- 创建管理员 -->
    <a-modal
      v-model="createManagerVisible"
      title="创建管理员"
      width="50%"
      @ok="createManager"
    >
      <a-form-model 
        :label-col="{
          span: 4
        }"
        :wrapper-col="{
          span: 18
        }"
      >
        <a-form-model-item label="账号">
          <a-input v-model="createParams.account" />
        </a-form-model-item>
        <a-form-model-item label="姓名">
          <a-input v-model="createParams.name" />
        </a-form-model-item>
        <a-form-model-item label="性别">
          <a-select
            v-model="createParams.gender"
            :options="gender"
          ></a-select>
        </a-form-model-item>
        <a-form-model-item label="手机号码">
          <a-input v-model="createParams.phone" />
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!-- 查看管理员 -->
    <a-modal
      v-model="viewManagerVisible"
      title="查看管理员"
      width="50%"
      :footer="null"
    >
      <a-form-model 
        :label-col="{
          span: 4
        }"
        :wrapper-col="{
          span: 18
        }"
      >
        <a-form-model-item label="账号">
          <div>{{ managerDetail.account }}</div>
        </a-form-model-item>
        <a-form-model-item label="姓名">
          <div>{{ managerDetail.name }}</div>
        </a-form-model-item>
        <a-form-model-item label="头像">
          <div v-if="managerDetail.avatar" class="avatar_container" :style="`background-image: url(${proxyPrefix + '/cbh_file/' + managerDetail.avatar})`" />
          <div v-else>暂时没有头像</div>
        </a-form-model-item>
        <a-form-model-item label="性别">
          <div>{{ managerDetail.gender }}</div>
        </a-form-model-item>
        <a-form-model-item label="手机号码">
          <div>{{ managerDetail.phone }}</div>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
export default {
  name: "Manager",
  components: { STable },
  data() {
    let params = {
      status: '',
      content: ''
    }

    let createParams = {
      account: '',
      name: '',
      gender: 1,
      phone: ''
    }
    let columns = [
      {
        title: '账号',
        dataIndex: 'account',
      },
      {
        title: '姓名',
        dataIndex: 'name',
      },
      {
        title: '性别',
        scopedSlots: { customRender: "gender"},
      },
      {
        title: '手机',
        dataIndex: 'phone'
      },
      {
        title: '用户状态',
        scopedSlots: { customRender: "status"},
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ] 
    return {
      params,
      columns,
      createParams,
      createManagerVisible: false,
      viewManagerVisible: false,
      loadData: paramters => {
        return this.getManagerList(Object.assign({}, this.params, paramters))
      },
      proxyPrefix: process.env.VUE_APP_API_URL,
      selectedRowKeys: []
    }
  },
  computed: {
    ...mapState('manager', ['managerList', 'managerDetail']),
    status() {
      return [
        {
          label: '全部',
          value: ''
        },
        {
          label: '正常',
          value: 1
        },
        {
          label: '已禁用',
          value: 2
        }
      ]
    },
    gender() {
      return [
        {
          label: '男',
          value: 1
        },
        {
          label: '女',
          value: 2
        }
      ]
    }
  },
  methods: {
    ...mapActions('manager', {
      getManagerList: 'getManagerList',
      addManager: 'addManager',
      getManagerDetail: 'getManagerDetail',
      delManager: 'deleteManager',
      ban: 'ban'
    }),
    toCreateManager() {
      this.createManagerVisible = true
    },
    createManager() {
      this.addManager(this.createParams).then(res => {
        this.createManagerVisible = false
        this.$message.success('创建成功')
      })
    },
    viewManagerDetail(id) {
      this.getManagerDetail(id).then(res => {
        this.viewManagerVisible = true
      })
    },
    banManager(text, status) {
      let id = []
      if (text instanceof Array) {
        id = [...text]
      } else {
        id.push(text.id)
        status = text.status === 1 ? 2 : 1
      }
      this.ban({
        id,
        status,
      }).then(res => {
        this.$message.success('操作成功')
        this.$refs.table.refresh(true)
      })
    },
    search() {
      this.$refs.table.refresh(true)
    },
    deleteManager(id) {
      this.delManager(id).then(res => {
        this.$refs.table.refresh(true)
        this.$message.success('删除成功')
      }).catch(err => {
        this.$message.error('删除失败')
      })
    },
    viewRecord() {
      this.$router.push({
        name: 'Manager Record'
      })
    },
    reset() {
      this.params = {
        status: '',
        content: ''
      }
      this.$refs.table.refresh(true)
    },
    onSelectChange(val) {
      this.selectedRowKeys = val
    }
  }
}
</script>
<style lang="scss" scoped>
.avatar_container {
  background-position: center;
  background-size: cover;
  width: 88px;
  height: 88px;
}
</style>